.setting-calendar {
    background: #FFF;
}

.setting-calendar .palet-header {
    background-color: #d5414f;
    height: 10rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 4rem;
}

.setting-calendar .palet-header img {
    width: 2rem;
    height: 3.6rem;
}

.setting-calendar .palet-header span {
    font-size: 3.4rem;
    color: #fff;
    font-weight: 600;
    padding-left: 8rem;
}

.setting-calendar .setting-calendar-content {
    padding: 2.5rem 4rem 3rem 4rem;
}

.setting-calendar .setting-calendar-content .setting-calendar-items {
    padding: 2.5rem 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    position: relative;
}

.setting-calendar .setting-calendar-content .setting-calendar-items .items-color {
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
}

.setting-calendar .setting-calendar-content .setting-calendar-items .items-text {
    padding-left: 7rem;
    font-size: 3rem;
}

.setting-calendar .setting-calendar-content .setting-calendar-items .items-active img {
    width: 4.5rem;
    height: 3rem;
    margin-bottom: 3rem;
}

.setting-calendar .setting-calendar-content .setting-calendar-items .items-active {
    position: absolute;
    right: 0;
}


