.calendar-monthly {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    /*overflow-y: scroll;*/
}

.calendar-monthly .rbc-calendar {
    /*height: 103rem;*/
    height: 90rem;
    min-height: 90rem;
}

.calendar-monthly .custom-toolbar {
    height: 10rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    padding-right: 4rem;
    padding-left: 3rem;
    position: relative;
}
.calendar-monthly .custom-toolbar .prev-toolbar img,
.calendar-monthly .custom-toolbar .next-toolbar img{
    height: 3rem;
    width: 2.3rem;
}

.calendar-monthly .custom-toolbar .prev-toolbar,
.calendar-monthly .custom-toolbar .next-toolbar{
    position: absolute;
    top: 3.7rem;
    height: 4rem;
    width: 4rem;
}

.calendar-monthly .custom-toolbar .prev-toolbar{
    left: 20rem;
}

.calendar-monthly .custom-toolbar .next-toolbar{
    right: 18rem;
}

.calendar-monthly .custom-toolbar .left-toolbar {
    width: 9.5rem;
}

.calendar-monthly .custom-toolbar .left-toolbar .date {
    font-size: 2.5rem;
    width: 4.8rem;
    height: 5rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: url("../../../icons/ic_date_48px.png") no-repeat;
    background-size: 4.8rem 4.7rem;
}

.calendar-monthly .custom-toolbar .center-toolbar {
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    position: absolute;
    left: 37%;
}

.calendar-monthly .custom-toolbar .center-toolbar .year {
    font-size: 2.6rem;
    font-weight: bold;
    color: #cccccc;
    margin-right: 2rem;
}

.calendar-monthly .custom-toolbar .center-toolbar .month {
    font-size: 4.3rem;
    color: #333333;
}

.calendar-monthly .custom-toolbar .right-toolbar {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding-bottom: 1.5rem;
    text-align: right;
    font-size: 5rem;
    font-weight: bold;
    color: #666666;
    position: absolute;
    right: 2rem;
    top: 0;
}

.rbc-month-header {
    height: 5.6rem;
    line-height: 5.6rem;
    font-size: 2.4rem;
}

.rbc-month-header .rbc-header {
    color: #aaaaaa;
    font-weight: normal;
}

.calendar-monthly .rbc-month-row .rbc-row-content {
    height: 100%;
}

.calendar-monthly .rbc-month-row .rbc-row-content .rbc-row:first-child {
    height: 5.5rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.calendar-monthly-list .custom-toolbar{
    position: fixed;
    width: 100%;
    z-index: 1200;
    background-color: #e9e9e9;
}

.rbc-current {
    position: relative;
}

.rbc-row-content .rbc-current::after {
    content: "";
    position: absolute;
    background: #D9F3F6;
    z-index: -1;
    height: 500%;
    width: 100%;
    outline: none;
    border: none;
    left: 0;
    top: -0.7rem;
}
.rbc-time-header{
    max-height: 60%;
    overflow-y: scroll;
}

.calendar-monthly .rbc-date-cell {
    text-align: center;
    padding-right: 0.5rem;
}

.calendar-monthly .rbc-off-range-bg {
    background-color: #ffffff;
}

.calendar-monthly .rbc-event {
    text-align: center;
    height: 2.6rem;
    border-bottom: 0.1rem solid #e9e9e9;
    border-top: 0.1rem solid #e9e9e9;
    margin-top: 0.1rem;
    padding: 0;
    border-radius: 0;
}

.calendar-monthly .rbc-date-cell .date {
    font-size: 2.88rem;
}

.calendar-monthly .rbc-day-bg {
    background-color: #ffffff;
}

.calendar-monthly .rbc-now .date {
    width: 5rem;
    height: 5rem;
    background-color: #00b1c3;
    border-radius: 50%;
    color: #ffffff !important;
    padding: 0.5rem;
    font-weight: normal;
}

.calendar-monthly .rbc-event-content {
    font-size: 2rem;
}

.calendar-monthly .rbc-row-content .rbc-date-cell:nth-child(6) a {
    color: #00b1c3;
}

.calendar-monthly .rbc-row-content .rbc-date-cell:nth-child(7) a {
    color: #e14a56;
}

.calendar-monthly .event-table {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin-top: 4rem;
    -ms-flex: 1 1;
        flex: 1 1;
}

.calendar-monthly .left-content {
    width: 10rem;
    text-align: center;
}

.calendar-monthly .left-content .event-date {
    font-size: 4.5rem;
    font-weight: bold;
}

.calendar-monthly .left-content .event-day {
    font-size: 2.8rem;
    color: #cccccc;
}

.calendar-monthly .right-content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.calendar-monthly .step-item {
    height: 10.5rem;
    width: 60rem;
    position: relative;
    margin-bottom: 2.5rem;
    left: 3.3rem;
}

.calendar-monthly .step-circle {
    position: absolute;
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    top: 4.5rem;
    left: -4rem;
}

.calendar-monthly .step-line {
    position: absolute;
    z-index: -1;
    width: 0.5rem;
    height: 13rem;
    background: #dddddd;
    top: 5rem;
    left: -3.5rem;
}

.right-content a:last-child .step-line {
    display: none;
}

.calendar-monthly .event-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    color: #ffffff;
    height: 100%;
    font-size: 3.2rem;
    -ms-flex-align: center;
        align-items: center;
}

.calendar-monthly .event-info .time {
    width: 10rem;
    text-align: center;
    border-right: 0.2rem solid #ffffff;
}

.calendar-monthly .event-info .event-content {
    padding-left: 3rem;
}

.calendar-monthly .rbc-show-more{
    margin-top: 0.1rem;
    pointer-events: none;
}




