}

.schedule-entry-popup .data_img .data .date {
    font-size: 4rem;
    color: #333333;
    font-weight: 500;
}

.schedule-entry-popup .data_img .data .date span {
    font-size: 2.8rem;
}

.schedule-entry-popup .data_img .data .date .money {
    color: #999999;
}

.schedule-entry-popup .data_img .data .hours .time {
    font-size: 3.2rem;
    color: #999999;
}

.schedule-entry-popup .data_img .data .hours span {
