    background-image: -o-linear-gradient(left, #28cbd5, #5debdc);
    background-image: linear-gradient(to right, #28cbd5, #5debdc);
    border-radius: 50%;
    position: relative;
}

.schedule-entry-popup .footer_plus img {
    width: 3.2rem;
    height: 3.2rem;
