}

.schedule-entry-popup #start_time .time {
    position: absolute;
    right: -3rem;
    top: -5rem;
}

.schedule-entry-popup #start_time {
    position: relative;
    top: 0;
    right: 0;
}

.schedule-entry-popup .img-schedule-popup {
    height: 20rem;
}

.schedule-entry-popup .active {
    background: #e9e9e9;
    border-bottom: 1px solid #FFF;
}



