.comment {
    padding-bottom: 7rem;
    border-bottom: 0.1rem solid #d3d3d3;
}

.comment .cap{
    font-size: 3rem;
    color: #999999;
    padding-bottom: 3.5rem;
    position: relative;
    border-bottom: 1px solid #f3f3f3;
}

.comment .cap .ic_comment{
    text-align: right;
}

.comment .cap .ic_comment img, .comment .form-control-start img{
    width: 4.9rem;
    height: 4.1rem;
}
.comment .cap .ic_comment span{
    padding-left: 1rem;
}

.comment .form-control-start {
    position: absolute;
    left: 4rem;
    z-index: 2;
    display: block;
    width: 3.4rem;
    height: 3.4rem;
    line-height: 3.4rem;
    text-align: center;
    pointer-events: none;
    top: 1.5rem;
}


.comment .form-control-feedback{
    top:1.5rem;
    right: 4rem;
}
.comment .form-control-feedback img{

}
.comment .form-control{
    height: 6.6rem;
    border-radius: 5rem;
    padding: 0 9.5rem;
    font-size: 2.8rem;
    color:#444444;
    background-color: #eaeaea;
}

.comment .form-group{
    font-size: 2.4rem;
    color:#444444;
    padding: 7rem 0 5rem 0;
}

.comment .user {
    margin-top: 3rem;
}
.comment .user img{
    width: 7.2rem;
    height: 7.2rem;
}
.comment .user .comment_item{
    padding: 2.5rem 3.4rem 3.4rem 3.4rem;
    border: 0.2rem solid #dddddd;
    border-radius: 2.5rem;
}
.comment .user .comment_item .ic_reply{
    padding-top: 2.3rem;
    text-align: right;
}
.comment .user .comment_item .ic_reply img{
    width: 14.5rem;
    height: 3rem;

}
.comment .user .comment_item .name{
    font-size: 2.6rem;
    color: #444444;
}
.comment .user .comment_item .name span{
    font-size: 2.2rem;
    color: #bbbbbb;
    padding-left: 2.2rem;
}

.comment .user .comment_item .date{
    font-size: 2.2rem;
    color: #999999;
    text-align: right;
}

.comment .user .comment_item .content{
    font-size: 2.8rem;
    color: #555555;
    padding-top: 3.3rem;
    text-align: justify;
}

.comment .more2{
    text-align: center;
    padding-top: 5rem;
}
.comment .search{
    padding-top: 4.5rem;
    position: relative;
}
.comment .ic_comment{
    position: absolute;
    right: 0;
}
.comment .search .ic_comment{
    position: absolute;
    left: 2rem;
    bottom: 1.2rem;
    width: 4.9rem;
    height: 4.1rem;
}
.comment .ic_delete{
    position: absolute;
    right: 2rem;
    bottom: 1.6rem;
    height: 3.4rem;
    width: 3.4rem;
}



