/* ------------------------------------------------------- */
.search_schedule {
    /*padding-bottom: 9rem;*/
    bottom: 0;
}

.search_schedule .schedule_item {
    /*padding-top: 2.3rem;*/
    display: -ms-flexbox;
    display: flex;
    padding-bottom: 5rem;
}
.search_schedule .schedule_item:first-child{
    padding-top: 2.3rem;
}



.search_schedule .schedule_item .left {
    color: #333333;
    font-size: 5.2rem;
    text-align: center;
    width: 13rem;
}

.search_schedule .schedule_item .left .inSession {
    font-size: 2.4rem;
    font-weight: bold;
    color: #ffffff;
    background-color: #28cbd5;
    padding: 0.4rem 0.5rem 0 0.5rem;
    margin: 0 1.3rem;
}

.search_schedule .schedule_item .right {
    padding-right: 4rem;
    width: 100%;
}

.search_schedule .schedule_item .gray {
    font-size: 2.4rem;
}

.search_schedule .schedule_item .schedule_content {
    font-size: 3rem;
    color: #555555;
}

.search_schedule .schedule_item .schedule_content .name {
    padding: 2.2rem 0;
}

.search_schedule .schedule_item .schedule_content .name .row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.search_schedule .schedule_item .schedule_content .name .private img {
    padding-right: 1.2rem;
}

.search_schedule .schedule_item .schedule_content .name .private {
    text-align: right;
}

.search_schedule .schedule_item .schedule_content .name .fullname {
    font-size: 2.6rem;
    color: #444444;
}

.search_schedule .schedule_item .schedule_content .name .username {
    font-size: 2.6rem;
    color: #bbbbbb;
}

.search_schedule .schedule_item .schedule_content .img {
    margin: 1.6rem 0;
    height: 28rem;
}

.search_schedule .schedule_item .schedule_content .img > img {
    width: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
    height: inherit;
}

.search_schedule .schedule_item .schedule_content .prince_icon {
    font-size: 3.2rem;
    border-top: 0.1rem solid #e9e9e9;
}

.search_schedule .schedule_item .schedule_content .prince_icon .prince {
    color: #e14a56;
}

.search_schedule .schedule_item .schedule_content .prince_icon .follow-number,
.search_schedule .schedule_item .schedule_content .prince_icon .like-number{
    padding-left: 0.5rem;
    padding-right: 1.5rem;
    min-height: 0.1rem;
    padding-top: 5px;
}

.search_schedule .schedule_item .schedule_content .prince_icon > .row {
    padding-top: 3.4rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}

.search_schedule .schedule_item .schedule_content .schedule .title {
    font-size: 2.6rem;
    color: #999999;
}

.search_schedule .schedule_item .schedule_content .schedule .content {
    font-size: 3.6rem;
    color: #555555;
    padding-left: 4rem;
    margin-top: 2rem;
    border-left: 0.4rem solid #28cbd5;
    font-family: "NotoSansCJKjp-Regular",sans-serif;
}

.search_schedule .schedule_item .schedule_content .schedule .content .date {
    font-size: 4.4rem;
    color: #333333;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 2rem;
    font-family: "Oswald-Medium",sans-serif;
}

.search_schedule .schedule_item .schedule_content .schedule .content .date .from {
    padding-right: 1.5rem;
}

.search_schedule .schedule_item .schedule_content .schedule .content .date .to {
    padding-left: 2rem;
}

.search_schedule .schedule_item .schedule_content .schedule .content .date span {
    font-size: 3rem;
}

.search_schedule .blue {
    color: #5e95d4;
}

.search_schedule .red {
    color: #e14a56;
}

.search_schedule .gray {
    color: #999999;
}

.search_schedule .schedule_item .schedule_content .schedule .content .house {
    font-size: 3.2rem;
    color: #999999;
    padding-top: 2rem;
    font-family: "Oswald-Regular",sans-serif;
    letter-spacing: 0.1rem;
}

.search_schedule .schedule_item .schedule_content .schedule .content .house span.status {
    font-size: 2.8rem;
    padding-left: 2rem;
    word-break: break-all;
}

.search_schedule .delete-ic {
    width: 3.2rem;
    height: 3.2rem;
}

.search_schedule .dropdown-ic {
    width: 2.2rem;
    height: 1.8rem;
}

.search_schedule .user-ic {
    width: 7.1rem;
    height: 7.1rem;
}

.search_schedule .ic_share,
.search_schedule .download-ic,
.search_schedule .public-ic,
.search_schedule .like-ic {
    width: 3.8rem;
    height: 3.8rem;
    margin-top: -0.3rem;
    margin-right: 1rem;
}

.search_schedule .ic_share{
    margin-top: -1.5rem;
}

.search_schedule .download-ic {
    margin-top: -0.8rem;
}


