@font-face {
    font-family: "Oswald-Medium";
    src: url('../font/Oswald-Medium.ttf') ;
}
@font-face {
    font-family: "Oswald-Regular";
    src: url('../font/Oswald-Regular.ttf') ;
}
@font-face {
    font-family: "Oswald-Light";
    src: url('../font/Oswald-Light.ttf') ;
}
@font-face {
    font-family: "NotoSansCJKjp-Medium";
    src: url('../font/NotoSansCJKjp-Medium.ttf') ;
}
@font-face {
    font-family: "NotoSansCJKjp-Regular";
    src: url('../font/NotoSansCJKjp-Regular.ttf') ;
}
@font-face {
    font-family: "hirakakupro-w3";
    src: url('../font/HiraKakuPro-W3.ttf');
}

html {
    font-size: 1.30208333vw;
    height: 100%;
    min-height: 100%;
}

html, body, .container, #root{
    height: 100%;
    min-height: 100%;
}

html body {
    height: 100%;
    min-height: 100%;
    font-size: 1.4rem;
    /** Support for scroll in IOS **/
    -webkit-overflow-scrolling: touch !important;
    font-family: "NotoSansCJKjp-Medium","NotoSansCJKjp-Regular",sans-serif;
}

.container {
    width: 100%;
    min-height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 0;
    margin: 0;
}

.modal-show>.content>.color-palet{
    margin-bottom: 11rem;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
    select,
    textarea,
    input {
        font-size: 16px!important;
    }
}

.container > .content {
    -ms-flex: 1 1;
        flex: 1 1;
    /*overflow-y: scroll;*/
    margin-bottom: 11rem;
}

@supports (-webkit-overflow-scrolling: touch) {
    .container > .content{
        padding-bottom: 16.5rem;
    }

    .container > .content .calendar-monthly{
        /*padding-bottom: 20rem;*/
    }

    .mypage #mypage-content {
        padding-bottom: 5.5rem;
    }
    .modal-show>.content>.color-palet{
        padding-bottom: 16.5rem;
    }

    .rec-calendar .cap-rec-calendar .loadMore,
    .rec-user .cap-rec-user .loadMore,
    .rec-schedule .cap-rec-schedule .loadMore,
    .calendar-monthly .custom-toolbar .center-toolbar
    {
        top: 2.5rem;
    }

    .schedule-new-entry .react-datepicker-wrapper input{

        -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
        box-sizing: border-box;
        padding-bottom: .3rem;
    }

    .edit-my-calendar .data{
        padding-bottom: 17rem !important;
    }


}

.menu {
    background: #ffffff;
    -ms-flex: 1 1;
        flex: 1 1;
    height: 100vh;
}

a:focus, a:hover {
    text-decoration: none;
}

[class*="col-"] {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    min-height: 0.1rem;
}

.d_flex_center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

/* form */
.form-control-feedback {
    width: auto;
    height: auto;
    line-height: 3.4rem;
}

.form-horizontal .form-group {
    margin-right: -1.5rem;
    margin-left: -1.5rem;
}

.form-group {
    margin-bottom: 1.5rem;
}

label {
    margin-bottom: 0.5rem;
}

.row {
    margin-right: -1.5rem;
    margin-left: -1.5rem;
}

.form-control {
    border-radius: 0;
}

.cancel button img,
.cancel-ic {
    width: 3rem;
    height: 3.2rem;
}

.modal-custom {
    width: 90%;
    top: 9rem;
    position: relative;
    background-color: #fff;
    padding: 2rem 0 0 0;
    height: 95rem;
    border-radius: 0.8rem;
    margin: auto;
}

.modal-page .search_header {
    height: 10rem;
    line-height: 10rem;
    display: -ms-flexbox;
    display: flex;
}

.modal-page .search_header .title {
    font-size: 3.55rem;

}

.search_header .cancel img {
    width: 3rem;
    height: 3rem;
    margin-top: -0.5rem;
}

.rbc-date-cell .rbc-now,
.rbc-date-cell.rbc-now {
    font-weight: bold;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.recommended .slider-frame{
    padding: 1px 0!important;
    min-height: 63rem;
}
.recommended .rec-user .slider-frame{
    min-height: 30rem;
}
.div-modal {
    display: block;
    width: 100vm;
    height: 100vh;
}

.form-control-feedback,
.form-control-feedback img {
    pointer-events: auto!important;
}

img.history-back {
    width: 2.1rem!important;
    height: 3.4rem!important;
}

/* css file */
.disable-link {
    pointer-events: none;
}

/* user */
.user-ic,
.user-info-ic {
    /*width: 7rem;*/
    /*height: auto;*/
    /*border-radius: 50%;*/
}

.circle{
    border-radius: 50%!important;
    width: 7rem !important;
    height: 7rem!important;
    overflow: hidden;
}

a.user-info {
    display: -ms-flexbox;
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

.mypage #mypage-content .col-xs-4>a.user-info{
    position: absolute;
    top: -8rem;
}

.search_schedule a.user-info,
.search_calendar a.user-info {
    margin-left: 1rem;
}

.info-area {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 8rem;
    -ms-flex-align: center;
        align-items: center;
}

.info {
    margin-left: 2rem;
}

.info-area div:last-child {
    font-size: 2.6rem;
    color: #bbbbbb;
}

.info-area div:first-child {
    font-size: 2.6rem;
    color: #444444;
}

.info-area div:last-child {
    font-size: 2.6rem;
    color: #bbbbbb;
}

.calendar_content .name,
.schedule_content .name {
    position: relative;
}

.calendar_content .name .private,
.schedule_content .name .private {
    position: absolute;
    right: 0.5rem;
    bottom: 5rem;
}

.calendar_content .name .private, .schedule_content .name .private {
    bottom: 2rem;
}

/* Image upload */
span.form-control{
    font-size: 2.7rem!important;
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding: 3rem 2rem;
}

.img_item .fileContainer{
    webkit-box-shadow: none;
    -webkit-box-shadow:none!important;
            box-shadow:none!important;
    border-radius:0!important;
    padding: 0!important;
    margin: 0!important;
    background: none!important;
}

.fileUploader .fileContainer .chooseFileButton{
    padding: 0;
    background: none;
    border-radius: 0;
    color: #CCC;
    font-weight: 300;
    font-size: inherit;
    margin: 0;
    -webkit-transition: all 0.2s ease-in;
    -o-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    cursor: pointer;
    outline: none;
    border: none;
}

.fileUploader .fileContainer .chooseFileButton:hover{
    background: none;
}

.img_item input[type=file]{
    display: none;
}

.bg-resize{
    background-repeat:no-repeat!important;
    background-size: cover!important;
    background-position: 100% 10%!important;
}

.title-slider{
    font-size: 3.6rem;
    text-align: center;
    color: #FFF;
    position: absolute;
    bottom: 11.7rem;
    left: 2.2rem;
}

.rbc-month-row:first-child{
    border-top: 1px solid #DDD;
}

.rbc-month-row:last-child{
    border-bottom: 1px solid #DDD;
}

.rbc-header + .rbc-header{
    border-left: none!important;
}

.rbc-month-view{
    border: none!important;
}

.calendar-detail .rbc-day-bg + .rbc-day-bg{
    border-left:none!important;
}

.content .react-datepicker-popper{
    z-index: 10000;
}

.user{
    position: relative;
}

.is-public{
    font-size: 2.2rem;
    color: #555555;
    text-align: right;
    position: absolute;
    right: -0.8rem;
    bottom: 4.5rem;
}

.is-public img{
    margin-right: 1rem;
}

.task-list{
    margin-left: 6rem;
    font-size: 2.5rem;
    color: #28cbd5;
    margin-top: 5rem;
}

.schedule-month-title{
    color: #FFFFFF;
}

.comment_item .info .fullname{
    display: inline;
    margin-right: 1.1rem;
}

.comment_item .info .username{
    display: inline;
    color: #999;
}
.calendar_content .content .date,
.schedule_content .content .date{
    /*font-weight: bold;*/
    font-size: 4.5rem;
}

.date-day{
    font-size: 5.7rem;
}

.add-schedule-button a {
    color: #28cbd5;
}

.search-close{
    display: none;
    width: 3.5rem;
    height: 3.5rem;
}

.download-number{
    color: #555;
}

.event-row .month-schedule ,
.calendar-monthly .month-schedule {
    overflow: scroll;
    height: 38rem;
}

.calendar-detail .cv_bnt_l .cv_bnt .btn-new-entry,
.schedule-detail .cv .container-fluid .cv_bnt2 .btn-new-entry,
.btn-new-entry{
    width: 51rem;
}

.help{
    color: #e9e9e9;
}

.form-control-content{
    height: auto;
    min-height: 15rem;
}

.slider-image {
    height: 41rem;
    background-size: cover;
    background-position: 100% 5%;
}

.slider-image-thumb {
    height: 20rem;
}

.image-cover-search{
    height: 28rem;
}

.slider-slide.slide-visible table td:first-child{
    width: 15%;
}

.slider-slide.slide-visible table td{
    width: 10%;
}

.word-break{
    word-break: break-all;
}
