.modal-page {
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    width: 100vw;
    bottom: 11rem;
    left: 0;
    /* height: 100%; */
    height: calc(100% - 11rem);
}

.modal-page-content {
    /* display: flex; */
    -ms-flex: 1 1;
        flex: 1 1;
    width: 100%;
    height: 100%;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.modal-hide {
    top: 100%;
    /*transition-duration: 0.8s;*/
    background: #ffffff;
    display: none
}

.modal-show {
    /*transition-duration: 0.8s;*/
    top: 0;
    background: #ffffff;
    z-index: 9991;
    overflow-y: scroll;
    display: -ms-flexbox;
    display: flex;
}

.h-100 {
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}



