.not-found{
    top: 0;
    width: 94%;
}

.not-found .cancel {
    padding: 0 !important;
}

.not-found .cancel button {
    text-align: left;
    background-color: #fff;
    padding-top: 0;
    height: auto;
    border: none;
}

.not-found .not-found-content .content span {
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin-bottom: 1rem;
}

.not-found .title {
    font-size: 9rem;
    color: #e9e9e9;
    text-align: center;
    padding-top: 55%;
    font-weight: 600;
}

.not-found .not-found-content {
    position: absolute;
    width: 100%;
    left: 0;
    right: 0;
    top: 16rem;
}


