.footer *{
  padding: 0;
  margin: 0;
}

.footer {
  height: 11.1rem;
  display: -ms-flexbox;
  display: flex;
  /*align-items: center;*/
  border-top: 0.1rem solid #d3d3d3;
  /*padding: 0 6rem;*/
  bottom: 0;
  width: 100vw;
  position: fixed;
  background-color: white;
}

.footer .btn {
  -ms-flex: 1 1;
      flex: 1 1;
  position: relative;
  text-align: center;
  /*border: 1px solid red;  */
  padding: 0 2.2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.footer .btn:first-child {
  text-align: center;
}

.footer .btn:last-child {
  text-align: center;
}

.footer .center-btn {
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
}

.footer .btn .calendar {
  background: url("../../../icons/btn.png") no-repeat;
  background-size: 9.5rem 9.5rem;
  height: 9.5rem;
  width: 9.5rem;
  color: #ffffff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.footer .notification {
  position: relative;
}

.footer .notification .barge {
  background-color: #e60000;
  color: #ffffff;
  font-size: 2.1rem;
  font-weight: bold;
  width: 3.8rem;
  height: 3.8rem;
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: absolute;
  left: 8.5rem;
  bottom: 5.5rem;
}

.footer .logo-ic,
.footer .user-ic,
.footer .alert-ic,
.footer .calendar-ic,
.footer .search-ic{
  width: 4.9rem;
  height: 4.9rem;
}

.footer .user-ic{
  border-radius: unset;
}

.footer .logo-ic{
  width: 6.8rem;
  height: 4.7rem;
}

.fix-bottom{
  position: relative;
  z-index: 9999;
}

.footer .btn:focus,
.footer .btn:hover
{
  text-decoration: none;
  border: 1px solid #eee;
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: #eee;
  outline: 0;
  border-radius: 0;
}

.footer .btn a:focus,
.footer .btn a:hover
{
  outline: 0;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  background: transparent;
}




