.edit-my-calendar {
    background: #FFF;
    width: 71rem;
    right: 0;
    position: absolute;
    height: 90%;
    overflow-y: scroll;
}

.edit-my-calendar .edit_header {
    /*height: 7rem;*/
}

.edit-my-calendar .edit_header img {
    margin: 3rem auto 1rem 3rem;
    width: 3rem;
    height: 3rem;
}
.edit-my-calendar .calendar_active,
.edit-my-calendar .calendar {
    height: 9rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 3rem 5rem;
}

.edit-my-calendar .calendar_active {
    color: #555555;
    font-size: 3rem;
    /*background-color: #f5fbfc;*/
    border-bottom: 1px solid #d3d3d3;
}

.edit-my-calendar .calendar_active img {
    margin-right: 4rem;
    width: 4.6rem;
    height: 4.7rem;
}

.edit-my-calendar .calendar {
    color: #555555;
    font-size: 3rem;
}

.edit-my-calendar .calendar img {
    margin-right: 4rem;
    width: 4.6rem;
    height: 4.7rem;
}

.edit-my-calendar .title_calendar {
    padding-top: 0.5rem;
    /*padding-bottom: 7rem;*/
    height:60rem;
    overflow-y:scroll;
}

.edit-my-calendar .title_calendar .items {
    padding: 5.5rem 4rem 0 4rem;
    color: #555555;
    font-size: 3rem;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.edit-my-calendar .title_calendar .items .items-text{
    color: #555555;
}

.edit-my-calendar .title_calendar .items .ic_color {
    width: 2.5rem;
    height: 2.5rem;
    margin-left: 1.5rem;
    margin-right: 5rem;
    border-radius: 50%;
}

.edit-my-calendar .title_calendar .items .ic_arrow_right {
    width: 1.4rem;
    height: 2.6rem;
    position: absolute;
    right: 4rem;
    top: 5.5rem;
}

.edit-my-calendar .data {
    padding-top: 0.5rem;
    /*padding-bottom: 7rem;*/
    margin-bottom: 5rem;
    border-bottom: none;
}

.edit-my-calendar .data .data_item {
    padding: 5.5rem 4rem 0 4rem;
    color: #555555;
    font-size: 3rem;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.edit-my-calendar .data .data_item .ic_icon {
    width: 4.8rem;
    height: 4.8rem;
    margin-right: 5rem;
}

.edit-my-calendar .data .data_item .ic_reload {
    margin-right: 5rem;
    width: 4.2rem;
    height: 4.8rem;
}

.edit-my-calendar .data .data_item .ic_arrow_right {
    width: 1.4rem;
    height: 2.6rem;
    position: absolute;
    right: 4rem;
}



