#share {
    top: 40rem;
}

#share .cancel img {
    text-align: left;
    background-color: #fff;
    /* padding: 0 0.2rem !important; */
    width: 2.2rem;
    height: 2.2rem;
    position: absolute;
    left: 3rem;
    top: 3.1rem;
}

#share .share-list > div {
    margin: 0 0 3rem 3rem;
}

#share .share-list-top > div {
    margin: 0 0 3rem 0;
}

#share .cancel {
    border-bottom: 1px solid #e9e9e9;
    height: 7rem;
    line-height: 5rem;
}

#share .field {
    padding-top: 7rem;
}

#share .content_page {
    font-size: 2.9rem;
    color: #555555;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 4.4rem 1rem 1rem 1rem;
}

#share .content_page span {
    color: #4392e3;
}

#share .cancel .title {
    font-size: 2.8rem;
    color: #1a1406;
    text-align: center;
    font-weight: 550;
    margin-left: 2rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

#share .alert-modal_content {
    position: absolute;
    width: 100%;
    left: 0;
}


