.sync-calendar-setting .header {
    border-bottom: 0.2rem solid #CCC;
    margin-bottom: 3rem;
}

.sync-calendar-setting-google {
    margin: auto;
    height: 50rem;
    width: 100%;
    text-align: center;
}

.sync-calendar-setting .title {
    font-size: 3.4rem;
    border-bottom: 1px solid #3333;
    padding: 1rem 4rem;
}

.sync-calendar-setting .calendar {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid #3333;
    padding: 4rem 4rem 4rem 3rem;
}

.sync-calendar-setting .calendar .cap {
    font-size: 3.4rem;
    color: #555;
}

.sync-calendar-setting .calendar img {
    position: absolute;
    right: 4rem;
    width: 1.5rem;
    height: 3rem;
}

.sync-calendar-setting button {
    font-size: 3.1rem;
    color: #fff;
    height: 9.4rem;
    border: none;
    border-radius: 5rem;
    width: 62%;
    margin: 17rem 0 0 0;
    cursor: pointer;
}

.sync-calendar-setting .title-top{
    font-size: 4rem;
    font-weight: 500;
    margin-left: 8rem;
}

.sync-calendar-setting .btn-m,
.sync-calendar-setting .fb {
    padding: 2rem 6rem 0 6rem;
}

.sync-calendar-setting .btn-m {
    text-align: center;
}

.sync-calendar-setting .btn-m button {
    background-image: -webkit-gradient(linear, left top, right top, from(#28cbd5), to(#5ceadc));
    background-image: -webkit-linear-gradient(left, #28cbd5, #5ceadc);
    background-image: -o-linear-gradient(left, #28cbd5, #5ceadc);
    background-image: linear-gradient(to right, #28cbd5, #5ceadc);
}

.sync-calendar-setting .btn-red button {
    background-image: -webkit-gradient(linear, left top, right top, from(#d52845), to(#ea5c5c));
    background-image: -webkit-linear-gradient(left, #d52845, #ea5c5c);
    background-image: -o-linear-gradient(left, #d52845, #ea5c5c);
    background-image: linear-gradient(to right, #d52845, #ea5c5c);
}

.sync-calendar-setting .fb {
    text-align: center;
}

.sync-calendar-setting .calendar-list {
    width: 73rem;
    margin: 2rem auto;
    padding: 0 4rem 1rem 4rem;
    position: relative;
    line-height: 6.5rem;
}

.sync-calendar-setting .form-control-feedback {
    right: 6rem;
    top: -1rem;
}

.sync-calendar-setting .title-calendar {
    font-size: 2.8rem;
}

.sync-calendar-setting .fb button {
    background-image: -webkit-gradient(linear, left top, right top, from(#4467b1), to(#7298e8));
    background-image: -webkit-linear-gradient(left, #4467b1, #7298e8);
    background-image: -o-linear-gradient(left, #4467b1, #7298e8);
    background-image: linear-gradient(to right, #4467b1, #7298e8);
}

.sync-calendar-setting .calendar-list-2 {
    width: 100%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    position: relative;
    /*display: flex;*/
}

.sync-calendar-setting .calendar-list-2 .form-label {
    position: absolute;
    right: 1rem;
    display: -ms-flexbox;
    display: flex;
    line-height: 10rem;
    top: 4rem;
}

.sync-calendar-setting .calendar-list-2 .title-calendar {
    position: absolute;
    left: 3rem;
    display: -ms-flexbox;
    display: flex;
    font-size: 3.5rem;
    top: 5rem;
}

.sync-calendar-setting .calendar-list label{
    float: right;
    padding-top: 1.4rem;
    line-height: 2rem;
}

.sync-calendar-setting .more2{
    padding-top: 2rem;
    padding-bottom: 5rem;
}

.sync-calendar-setting .react-switch-bg{
    height: 20px !important;
    width: 48px!important;
}

.sync-calendar-setting .react-switch-handle{
    height: 18px!important;
    width: 18px!important;
}


