.schedule-new-entry .slider {
    /*position: absolute;*/
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
}

.schedule-new-entry .slider:before {
    position: absolute;
    content: "";
    height: 4.8rem;
    width: 4.8rem;
    left: 0rem;
    bottom: 0rem;
    background-color: white;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    -webkit-box-shadow: 0rem 0.1rem 2rem 0.1rem #d6d6d6;
            box-shadow: 0rem 0.1rem 2rem 0.1rem #d6d6d6;
}

.schedule-new-entry input:checked + .slider {
    background-color: #4ad764;
}

.schedule-new-entry input:focus + .slider {
    -webkit-box-shadow: 0 0 0.1rem #4ad764;
            box-shadow: 0 0 0.1rem #4ad764;
}

.schedule-new-entry input:checked + .slider:before {
    -webkit-transform: translateX(4rem);
    -ms-transform: translateX(4rem);
    transform: translateX(4rem);
}

/* Rounded sliders */
.schedule-new-entry .slider.round {
    border-radius: 3.4rem;
