.view-place {
    padding-bottom: 6rem;
    border-bottom: 0.1rem solid #d3d3d3;
}

.view-place .cap {
    font-size: 3rem;
    color: #999999;
}

.view-place .text1 {
    font-size: 2.8rem;
    color: #444444;
    padding-top: 4.5rem;
}

.view-place .img_map {
    height: 18rem;
    width: 100%;
}

.view-place .text2 {
    font-size: 2.6rem;
    color: #999999;
    padding-top: 3rem;
    padding-bottom: 2rem;
}

.view-place .pin {
    font-size: 2.8rem;
    color: #4392e3;
    padding-top: 3.3rem;
    margin-top: 2rem;
}

.view-place .pin span {
    padding-right: 2rem;
}

.view-place .pin span img {
    width: 4.8rem;
    height: 4.8rem;
    margin-top: -1rem;
}


